import Vue from 'vue';
export default Vue.component('ant-icon', {
    functional: true,
    props: {
        type: {
            type: String,
            required: true
        }
    },
    render: function (h, context) {
        return h('i', {
            'class': {
                'anticon': true,
                [`anticon-${context.props.type}`]: true,
                [context.data.staticClass || '']: context.data.staticClass
            },
            style: context.data.style,
            on: context.data.on
        });
    }
});



